<template>
        <!--PARTIE 10 -> rajouter div v-if et div v-else -> divise le formaulaire-->
        <!-- edition v-if définie sur true (add) afin d'ajouter mais pas de modifier,
        par contre v-else défini sur false pour modifier -->

        <div v-if="!isEditing">

            <!-- PARTIE : -- 4 -- ajouter les deux input sans le div du dessus v-if sans @click= storeTodo
            -> ensuite
            ajouter todo en tableau vide dans data -->
            <input type='text' v-model="todo">
            <!-- V-MODEL : v-model crée une liaison de données sur les champs de formulaire 
            (input, select ou textarea) -->

            <!-- PARTIE : -- 6 -- ajouter click storTodo pour relier le bouton avec une method -->
            <input class="typebutton" type='submit' value="add" @click="storeTodo">
        </div>
        <!-- PARTIE 10 (2) rajouter le else -->
        <div v-else>
            <input type='text' v-model="todo">
            <input class="typebutton" type='submit' value="update" @click="updateTodo">
        </div>

        <ol>
            <!-- PARTIE : -- 1 -- créer le li avec todo avec v-for et l'index -->
            <li class="textwhite" v-for="(todo, index) in todos" :key="todo">
                {{ todo }}

            <!-- PARTIE 8 rajouter les deux boutons
            ensuiste ajouter les deux méthodes -->
                <button @click="editTodo(index, todo)">Edit</button>
                <button @click="deleteTodo(index)">Delete</button>
            </li>
        </ol>
</template>

<script>

export default {
    // PARTIE -- 2 -- ajouter le data avec return

    //data : Permet que les données et le DOM soient couplés
    data: () => {
        return {
            //PARTIE -- 10 -- rajouter isEditing et selectedIndex 
            isEditing: false,
            selectedIndex: null,
            //PARTIE -- 5 -- ajouter todo en tableau vide ds data
            todo: '',
            //PARTIE -- 3 -- ajouter "todos" avec les valeurs ds le tableau
            //Ensuite aller sur le navigateur pour voir que
            //nos données sont rendues
            todos: ['Learn Vue.js', 'Love chocolate'],
        }
    },
    methods: {
        //METHODES :
        // Les méthodes vous permettent de définir des fonctions auxquelles 
        // votre application Vue aura accès. 
        // Elles sont définies comme la propriété  data.
        
        //PARTIE --  7 -- ajouter une fonction dans method qui s'appelle storeTodo
        //Permet de relier notre bouton au faite d'ajouter le nouvel élement ds le tableau
        storeTodo() {

            // relie le bouton storeTodo

            // THIS : this est un opérateur qui permet de retourner 
            //une valeur en fonction de son contexte. 
            //this retourne l’objet 

            // PUSH : méthodes encapsulées qui va permettre d'ajouter 
            // des nouveaux élements ds la liste todos

            this.todos.push(this.todo)
            this.todo =''

            //le this todo ='' va permettre de l'enlever par la suite
        },

        //PARTIE 9 rajouter editTodo et updateTodo -> premet de relier le bouton editTodo
         editTodo(index, todo) {
            this.todo = todo
            //défini notre index
            this.selectedIndex = index
            //selectedIndex passe de null à true ici afin de pouvoir éditer
            //l'élement de la liste voulu
            this.isEditing = true
            //l'élément ici est sur true et reste ainsi ouvret à l'édition
        },

        //PARTIE 9 (2) -> relier le bouton update
        updateTodo() {
            //SPLICE : la méthode splice() permet d'insérer un élement à la place d'un autre
            //cette méthode écrase une tranche du tableau par une autre, splice attend un nombre
            //Ici splice supprime this.selectedIndex pour le remplacer par this.todo
            this.todos.splice(this.selectedIndex, 1, this.todo)
            this.isEditing = false
          
            //une fois que l'élement à été changé via update il repasse sur false
            //pour que notre bouton add revienne
        },


        // PARTIE 11 rajouter delete
        deleteTodo(index) {
        this.todos.splice(index, 1)
          //supprime un élement de l'index
    }
    }
}
</script>

<style>

    /* TO DO LIST STYLE*/

    button, .typebutton{
        background-color:rgb(228, 132, 22);
        border:none;
        border-radius: 20px;
        padding: 1.2rem 1.2rem;
        margin: 1rem 1rem;
        font-family: 'Satisfy', cursive;
        font-size: 1.5rem;
        color: white;
    }

    .textwhite{
        font-size: 1.5rem;
        font-family: 'IBM Plex Mono', monospace;
        color: white;
    }

</style>